.service {
  padding: 70px 15px 10px 15px;
  position: relative;
}
.service__title {
  font-family: "Rubik", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 28px;
  line-height: 33px;
  margin-top: 0;
  margin-bottom: 40px;
  text-align: center;
}
.service__item {
  background-color: #FFFFFF;
  margin-bottom: 30px;
  box-shadow: 0px 0px 25px rgba(208, 208, 208, 0.25);
  border-radius: 8px;
  box-sizing: border-box;
  padding: 25px;
  position: relative;
  z-index: 2;
}
.service__item::before {
  content: "";
  display: block;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: #FAC663;
  background-repeat: no-repeat;
  background-position: center;
  margin-bottom: 30px;
}
.service__item_temp::before {
  background-image: url(../assets/svg/temp.svg);
  background-size: 20px 36px;
}
.service__item_video::before {
  background-image: url(../assets/svg/video.svg);
  background-size: 31px 24px;
}
.service__item_taxi::before {
  background-image: url(../assets/svg/taxi.svg);
  background-size: 34px 20px;
}
.service__item_food::before {
  background-image: url(../assets/svg/food.svg);
  background-size: 35px 23px;
}
.service__item_walk::before {
  background-image: url(../assets/svg/walk.svg);
  background-size: 31px 26px;
}
.service__item_veterinar::before {
  background-image: url(../assets/svg/veterinar.svg);
  background-size: 27px 29px;
}
@media only screen and (min-width: 768px) {
  .service__item::before {
    width: 75px;
    height: 75px;
  }
  .service__item_temp::before {
    background-size: 26px 45px;
  }
  .service__item_video::before {
    background-size: 39px 30px;
  }
  .service__item_taxi::before {
    background-size: 42px 26px;
  }
  .service__item_food::before {
    background-size: 44px 29px;
  }
  .service__item_walk::before {
