.rooms {
    padding: 60px 15px 40px 15px;

    &__wrapper {
      max-width: 305px;
      margin: 0 auto;
    }

    &__title {
      font-family: $font-title;
      font-size: 28px;
      font-style: normal;
      font-weight: 700;
      line-height: 33px;
      margin-top: 0;
      margin-bottom: 10px;
    }

    &__fish {
      width: 150px;
      height: 46px;
      z-index: 1;
      margin: 0px 0px 0px -15px;
    }

    &__img_phone {
      margin-top: -18px;
      position: relative;
      z-index: 2;
    }

    &__img_ipad {
      display: none;
    }

    &__img_desktop{
      display: none;
    }

    &__wrapper-inn {
        padding: 25px;
      background-color: #FFFFFF;
    }

    &__title-room {
      font-family: $font-title;
      font-size: 20px;
      font-style: normal;
      font-weight: 500;
      line-height: 24px;
      margin-bottom: 25px;
    }

    &__items {
      text-decoration: none;
      margin-bottom: 25px;
      padding: 0;
    }

    &__item {
      font-style: $font-txt;
      font-size: 15px;
      line-height: 18px;
      margin-bottom: 17px;
      list-style-type: none;
    }

    &__item:last-child {
      margin-bottom: 0;
        
    }

    &__item::before {
      display: block;
      content: "";
      width: 11px; 
      height: 11px;
      background: #FAC663;
      border-radius: 50%;
      margin-top: 2px;
      margin-right: 10px;
      float: left;
    }
    
    &__btn-orange-inner {
      width: 240px;
      height: 50px;
      position: relative;
      background-color: #FF7236;
      border: none;
      border-radius: 25px;
      box-shadow: 0px 5px 25px rgba(26, 33, 47, 0.1);
      text-align: center;
      display: block;
      margin: 0 auto;
      font-style: normal;
      font-weight: 600;
      line-height: 19px;
      color: #FFFEFD;
      letter-spacing: 0em;
      padding-left: 50px;
      padding-right: 80px;
      box-shadow: 0px 5px 25px 0px rgba(26, 33, 47, 0.1);
    }
    
    &__btn-orange-svg {
      position: absolute;
      top: 7px;
      right: 7px;
    }
}

@media screen and (min-width: 768px) {
    .rooms {
      padding: 10px 45px;
      margin-top: 10px;
      margin-bottom: 10px;

      &__wrapper {
        max-width: 723px;
      }

      &__title {
        font-size: 36px;
        line-height: 42px;
        text-align: center;
        margin-bottom: 7px;
      }

      &__fish {
        width: 339px;
        height: 106px;
        position: relative;
        margin-left: -70px;
        margin-bottom: -50px;
        z-index: 1;
      }

      &__img_phone {
        display: none;
      }

      &__img_ipad {
        display: block;
        position: relative;
        z-index: 2;
        margin-left: -30px;
      }

      &__img_desktop {
        display: none;
      }

      &__wrapper-inn {
        width: 300px;
        height: 245px;
        position: relative;
        z-index: 3;
        margin-top: -310px;
        margin-left: 330px;
        border-radius: 8px;
        box-shadow: 0px 0px 25px rgba(208, 208, 208, 0.25);
      }
    }
}

@media screen and (min-width: 1366px) {
    .rooms {
        padding: 30px;

        &__wrapper {
          max-width: 1198px;
        }

    
        &__fish {
          width: 484px;
          height: 151px;
          display: block;
          position: relative;
          margin-left: -30px;
          z-index: 1;
          }
    
        &__img_phone {
          display: none;
          }
    
        &__img_ipad {
          display: none;
          }
    
         &__img_desktop {
          display: block;
          position: relative;
          z-index: 2;
          margin-top: -100px;
          margin-left: 135px;
          }

         &__wrapper-inn {
          width: 500px;
          position: relative;
          z-index: 3;
          margin-top: -340px;
          margin-left: 700px;
        }

        &__btn-orange-inner {
          position: relative;
        }
    }
} 